.container {
  width: 100%;
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 0 10px;
  border-radius: 7px;

  transition: background-color 0.3s;

  &:hover,
  &:focus-within {
    background-color: white;
  }

  input {
    width: 100%;
    background-color: transparent;
  }
}
